@import url(https://fonts.googleapis.com/css2?family=Skranji:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel:wght@400;500;600;700;800;900&display=swap);
body {
    margin: 0;
    font-family: 'Cinzel', serif;
    font-weight: 400 !important;
}

.main {
    overflow-x: hidden;
    overflow-y: hidden;
    height: 100vh;
    /*background-size: cover !important;*/
    /*background-color: #FF819E !important;*/
    /*background-image: url("/personal_page/img/vk.jpg");*/
    background-size: auto !important;
}

#main-wrapper {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;
    z-index: 1;
    /*background-size: auto !important;*/
}

#main-wrapper > video {
    position: absolute;
    min-width: 100%;
    min-height: 100%;
    width: auto;
}

.main-bg {
    background-color: #69599a !important;
    /*background: url('/personal_page/img/main.jpg') center #69599a;*/
}

.vk-bg {
    background-color: #7f3c85 !important;
    /*background: url('/personal_page/img/vk.jpg') center #7f3c85;*/
}

.tg-bg {
    background-color: #360e1f !important;
    /*background: url('/personal_page/img/tg.jpg') center #360e1f;*/
}

.gh-bg {
    background-color: #5c2432 !important;
    /*background: url('/personal_page/img/gh.jpg') center #5c2432 ;*/
}


@supports (object-fit: cover) {
    #main-wrapper > video {
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

#layout-opacity {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.4);
}

#menu-wrapper {
    display: flex;
    align-content: center;
    flex-direction: row;
}

#menu {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10rem;
}

.link-wrapper p {
    font-size: 2rem;
    color: white;
}

.link-container {
    border: 1px solid transparent;
    border-radius: 30px;
    padding: 0 0.5rem 0;
    display: flex;
    justify-content: center;
    transition: 0.2s;
}

.link-container:hover {
    border: 1px solid white;
}

.logo-wrapper {
    display: flex;
    justify-content: center;
    margin-bottom: 1rem;
}

.logo {
    width: 10rem;
    -moz-transition: all 0.1s ease-out;
    -o-transition: all 0.1s ease-out;
    -webkit-transition: all 0.1s ease-out;
    opacity: 0.75;
}

.logo:hover {
    opacity: 1;
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -o-transform: scale(1.1);
}

@media (max-width: 768px) {
    #menu {
        width: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-gap: 1.5rem;
    }

    .link-wrapper p {
        font-size: 1.5rem;
        color: white;
    }

    .logo {
        width: 7rem;
    }
}

p, h1 {
    text-shadow: -.0625rem -.0625rem 0 #6BFFFF, .0625rem .0625rem 0 #FF819E;
}

h1 {
    font-weight: 400 !important;
    /*font-family: 'Skranji', cursive;*/
    color: #ffffff;
    font-size: 2rem;
}

.footer-wrapper-wrapper {
    position: fixed;
    bottom: 0;
    /*margin-bottom: 4rem;*/
    width: 80%;

}

.footer-wrapper {
    display: flex;
    justify-content: center;
    width: 100%;
}

.footer-container {
    display: flex;
    justify-content: center;
    align-content: center;
    text-align: center;
    width: 10%;
}

.footer-container h1 {
    letter-spacing: 1rem !important;
}

@media (max-width: 425px) {
    .logo {
        width: 5rem;
    }

    .footer-container h1 {
        letter-spacing: 0.4rem !important;
    }
}

a {
    color: transparent;
    text-decoration: none;
}


